import { Zstd } from "@hpcc-js/wasm-zstd";
// 
export async function loadFileAsUint8Array(url) {
    const response = await fetch(url);
    return new Uint8Array(await response.arrayBuffer());
}
// zstd
export async function zstdDecompress(compressed) {
    const zstd = await Zstd.load();
    try {
        // zstd.decompress()  zstd 
        const decompressedData = await zstd.decompress(compressed);
        return new TextDecoder('utf-8').decode(decompressedData);
    }
    catch (err) {
        console.error(":", err);
        return null;
    }
}
// zstd
export async function zstdCompress(text) {
    const zstd = await Zstd.load();
    try {
        // UTF-8Uint8Array
        const input = new TextEncoder().encode(text);
        // zstd.compress() zstd
        return await zstd.compress(input, 22);
    }
    catch (err) {
        console.error(":", err);
        return null;
    }
}
